.place {
  &__photo-container {
    overflow: hidden;
    position: relative;
    width: 100%;
    max-height: 256px;
  
    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }
  
    @include onDesktop {
      height: 256px;
      padding-bottom: 0;
    }
  }
  
  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 8px;
  
    object-fit: cover;
    object-position: center;
  }
    
  &__photos {
    margin-bottom: 16px;
  
    @include onDesktop {
      margin-bottom: 24px;
    }
  }
  
  &__description-link {
    color: $colorBlue;
    text-decoration: none;
    font-weight: 700;
  }
}
